import './../../scss/menu.scss'
import home from './home.png'
import laser from './laser.png'
import cancel from './cancel.png'
import body from './body.png'
import face from './face.png'
import {useSelector, useDispatch} from "react-redux";
import {switcher} from "../../redux/slices/menuSlice"
import {Link} from "react-router-dom";


function Menu() {

    const active = useSelector(state => state.menu.value);
    const dispatch = useDispatch();

    return (
        <div className={active ? "menu__wrapper active" : "menu__wrapper"} onClick={() => dispatch(switcher(active))}>
            <div className="menu__content">
                <div className="menu__wrapper__cancel" ><img src={cancel} alt="" className='menu__img__cancel' /></div>
                <ul>
                    <li><Link to="/"> <img src={home} alt="img" />  </Link></li>
                    <li><Link to="/admin"> <img src={home} alt="img" />   </Link></li>
                    <li><Link to="/order"> <img src={home} alt="img" />  </Link></li>
                    <li><Link to="/face"> <img src={face} alt="img" />  </Link></li>
                    <li><Link to="/device-cosmetology"> <img src={laser} alt="img" />  </Link></li>
                    <li><Link to="/massage"> <img src={body} alt="img" />  </Link></li>
                </ul>
            </div>
        </div>
    );
}

export default Menu;